'use client'

import { useEffect, useState } from 'react'
import Body from './TerminalBody'
import NavBar from './TerminalNavBar'
import Text from './TerminalText'

interface TerminalProps {
	name: string
	hiddenTerminal: () => void
}

export default function Terminal({ name, hiddenTerminal }: TerminalProps) {
	const [fullTerm, setFullTerm] = useState(false)

	useEffect(() => {
		if (fullTerm) {
			document.body.style.overflow = 'hidden'
		} else {
			document.body.style.overflow = 'unset'
		}

		return () => {
			document.body.style.overflow = 'unset'
		}
	}, [fullTerm])

	const toggleFullTerminal = () => {
		setFullTerm(prev => !prev)
	}

	const getTerminalStyle = () => {
		if (fullTerm) {
			return 'w-screen h-screen fixed inset-0 z-50 rounded-none'
		}
		return 'w-full max-w-4xl xl:max-w-5xl h-80 md:h-96 lg:h-[28rem] rounded-2xl mb-8'
	}

	return (
		<div className='w-full min-h-screen bg-gradient-to-br from-black via-gray-900 to-black relative overflow-hidden'>
			{/* –ü–æ—á—Ç–∏ –Ω–µ–≤–∏–¥–∏–º—ã–π –∞–Ω–∏–º–∏—Ä–æ–≤–∞–Ω–Ω—ã–π —Ñ–æ–Ω */}
			<div className='absolute inset-0'>
				<div className='absolute top-1/4 left-1/4 w-72 h-72 bg-blue-800/3 rounded-full animate-float-1 blur-3xl'></div>
				<div className='absolute bottom-1/3 right-1/4 w-96 h-96 bg-purple-800/3 rounded-full animate-float-2 blur-3xl'></div>
				<div className='absolute top-1/2 left-1/3 w-80 h-80 bg-cyan-800/3 rounded-full animate-float-3 blur-3xl'></div>
			</div>

			<style jsx>{`
				@keyframes float-1 {
					0%,
					100% {
						transform: translate(0px, 0px) scale(1);
					}
					50% {
						transform: translate(-20px, -30px) scale(1.1);
					}
				}
				@keyframes float-2 {
					0%,
					100% {
						transform: translate(0px, 0px) scale(1);
					}
					50% {
						transform: translate(30px, -20px) scale(1.05);
					}
				}
				@keyframes float-3 {
					0%,
					100% {
						transform: translate(0px, 0px) scale(1);
					}
					50% {
						transform: translate(-15px, 25px) scale(1.08);
					}
				}
				.animate-float-1 {
					animation: float-1 15s ease-in-out infinite;
				}
				.animate-float-2 {
					animation: float-2 20s ease-in-out infinite 5s;
				}
				.animate-float-3 {
					animation: float-3 18s ease-in-out infinite 3s;
				}
			`}</style>

			<div
				className={`${
					fullTerm ? 'w-full h-full' : 'container mx-auto px-4 sm:px-6 lg:px-8'
				} flex flex-col items-center justify-center relative z-10 min-h-screen`}
			>
				<div
					className={
						getTerminalStyle() +
						' bg-black/20 backdrop-blur-lg shadow-xl shadow-black/30 border border-gray-700/30 cursor-text transition-all duration-300 flex flex-col'
					}
				>
					<NavBar
						hiddenTerminal={hiddenTerminal}
						fullTerminal={toggleFullTerminal}
						windowName='–¢–µ—Ä–º–∏–Ω–∞–ª'
						isFullScreen={fullTerm}
					/>
					<Body name={name} isFullScreen={fullTerm} />
				</div>

				{!fullTerm && <Text />}
			</div>
		</div>
	)
}
